.BtnDescarga{
    margin: 0px 10px 25px;
    height: 30px;
    background-color: #fff;
    cursor: pointer;
}
.BtnDescarga>label{
    display: block;
}
.BtnDescarga>img{
    height: 100%
}
.Paso2{
    margin: 0 auto;
    width: 90%;
    border-radius: 10px;
    border: 2px solid #eaeaea;
    padding: 2vmin;
    box-shadow: 2px 2px 1px #eaeaea;
    text-align: right;
    position: relative;
    display: grid;
    grid-template-columns: repeat(1,1fr);
}

.Paso2>div>a{
    display: block;
    margin: 0 auto;
}

.Paso2>div>label,.Paso2>div>select,.Paso2>div>input{
    display: block;
    text-align: left;
    width: 100%;
}
.Paso2>div>label::before{
    content: "*";
    color: rgba(255, 0, 0, 0.5);
    padding-right: 5px;
}
.Paso2>div>input{
    width: 95%;
}
@media (min-width:720px){
    .Paso2{
        grid-template-columns: repeat(2,1fr);
    }
}