/*------  Alertas  ------------*/

#containerForm{
    position:fixed;
    left:0;
    top:0;
    width:100%;
    height: 100vh;
    z-index: 1;
    display:flex;
    flex-flow: row wrap;
    overflow-y: auto;
    overflow-x: hidden;
    justify-content: center;
    align-content: flex-start;
    background: rgba(0, 0, 0, 0.5);
    padding: 2vmin 0;
}
#containerForm::-webkit-scrollbar{
    background: transparent;
    width:3vmin;
    border-right:5px solid transparent;
}
#containerForm::-webkit-scrollbar-thumb{
    background: var(--active);
    border-right:1vmin solid white;
}
#containerForm > h2{
    color:rgb(30, 68, 190);
    font-weight: bold;
    padding: 0 1vmin;
    width:100%;
}
#containerForm > span{
    width:6vmin;
    height:6vmin;
    margin-left:85%;
    border-radius:50%;
    display:flex;
    justify-content: center;
    align-items: center;
    box-shadow:0px 0px 1px red;
    cursor:pointer;
    font-weight: bold;
    color:red;
    background:var(--active);
    background:rgba(0, 0, 0, 0.541);
}
#containerForm > form{
    width:95%;
    background: white;
    min-width:300px;
    display: flex;
    flex-flow:column;
    align-items: center;
    align-content: center;
    padding:2vmin;
    margin-top:2vmin;
}
#containerForm > form > select,label,option{
    text-transform: capitalize;
}

#containerForm > form > select,input{
    width:100%;
    display: block;
    text-align: left;
    width: 100%;
    border: 1px solid #eaeaea;
    background-color:var(--background-color);
    color: #666;
    padding: .75em;
    border-radius: 5px;
    font-size: 1rem;
    margin: 5px 0px;
}
#containerForm > form > label{
    display: block;
    text-align: left;
    width: 100%;
    line-height: 1;
}

@media (min-width:720px){
    #containerForm > form{
        width:45%;
    }
}
