nav{
    background-color: var(--theme-color);
    width: 100%;
    height: 100px;
    background-image: none;
    position: relative;
    display: grid;
    grid-template-columns: 300px auto;
    align-items: center;
    box-shadow: 0 0 3px white;
    z-index: 1000;
    overflow: hidden;
}


nav>img{
    height: 80px;
    margin: 10px 20px;
}
nav>div{
    width: 100% ;
    display: block;
    text-align: right;
    background-color: transparent;
}
nav>div>a{
    color: white;
    padding: 0 10px 0 10px;
    display: inline-table;
    font-weight: bolder;
    font-size: 18px;
    text-transform: uppercase;
}
nav>div>a:last-child{
    position: relative;
    background-color: rgba(255,255,255,0.9);
    color: var(--theme-color);
    padding: 7px 35px 7px 7px;
    margin-right: 40px;
    border-radius: 10px;
    transition: all 0.2s;
}
nav>div>a:last-child::after{
    content: "";
    width: 50px;
    height: 100%;
    background-image: url(../../svg/usuario.svg);
    background-size: 25px;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 0;
    right:-7.5px;
}
nav>div>a:last-child:hover{
    background-color: rgba(255,255,255,1);
}
nav>div>a:last-child:active{
    background-color: rgba(255,255,255,9);
}
nav>span{
    display:none;
}
@media only screen and (max-width:1024px) {
    nav>div>a{
        font-size: 14px;
        padding: 5px;
    }
    
}
@media (max-width:768px){
    nav{
        align-items: flex-start;
        height: 60px;
        position: fixed;
        top:0;
}

.show{
    height:375px;
}
nav>span{
    color:white;
    padding: 5px;
    position: absolute;
    right:5%;
    font-size: 20px;
    font-weight: bold;
    top:10px;
    cursor:pointer;
    display:block;
}
nav>img{
    height: 50px;
    margin:  5px;
}
nav>div{
    width: 100%;
    position: absolute;
    top: 60px;
}
nav>div>a{
    display: block;
    position: relative;
    width: 100%;
    font-size: 18px;
    line-height: 2.4;
    text-align: center;
    background-color: rgba(0,0,0,0.1);
    border-bottom: 1px solid rgba(255,255,255,0.5);
}
    
nav>div>a:last-child{
    width: 130px;
    margin: 0 auto;
    padding: 3px 15px 0 0;

}
}
