@font-face{
  font-family: 'roboto';
  src: url('./components/roboto_r.eot');
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  border:none;
  font-family: 'roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: all .5s linear;
  word-wrap: break-word;
  font-size: var(--font-size);
  color:var(--font-color);
}
:root{
  --font-size:16px;
  --hHeader:10vh;
  --padTemp:2vmin;
  --theme-color:rgb(16, 108, 160);
  --background-color:rgb(241, 240, 241);
  --font-color:rgb(122, 122, 122);
  --active:rgb(16, 108, 160);
  --activeHover:rgb(11, 123, 186);
  --activeClick:rgb(17, 146, 219);
  --submit:rgb(97, 206, 112);
  --submitHover:rgb(68, 201, 85);
  --submitClick:rgb(53, 170, 68);
  --cancel:rgb(255, 17, 0);
  --cancelHover:rgb(255, 94, 0);
  --cancelClick:rgb(255, 17, 0);
}
.scroll,body,div,html{
  scroll-behavior: smooth;
}
.scroll::-webkit-scrollbar{
  background:transparent;
  width:5px;
}
.scroll::-webkit-scrollbar-thumb{
  background:var(--submitHover);
}
body::-webkit-scrollbar{
  background:transparent;
  width:5px;
}
body::-webkit-scrollbar-thumb{
  background:var(--submitHover);
}
@media(min-width:280px){
  :root{
    --hHeader:10vh;
    --padTemp:2vmin;
  }
}
@media(min-width:700px){
  :root{
    --hHeader:13vh;
    --padTemp:14px;
  }
}

@media(min-width:1024px){
  :root{
    --font-size:3vmin;
    --hHeader:13vh;
    --padTemp:11px;
  }
}
.btnGreen,.btnBlue,.btnRed{
  line-height: 2;
  padding: 0 2vmin;
  color: #fff;
  font-size: 16px;
  text-decoration: none;
  text-transform: capitalize;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.1s;
  border-bottom: 2px solid rgba(255,255,255,0);
}
.btnGreen:hover,.btnBlue:hover,.btnRed:hover{
  border-bottom: 2px solid grey;
}
.btnGreen{
  background-color: var(--submit);
}
.btnGreen:hover{
  background-color: var(--submitHover);
}
.btnGreen:active{
  background-color: var(--submitClick);
}
.btnBlue{
  background-color: var(--active);
}
.btnBlue:hover{
  background-color: var(--activeHover);
}
.btnBlue:active{
  background-color: var(--activeClick);
}

.btnRed{
  background-color: var(--cancel);
}
.btnRed:hover{
  background-color: var(--cancelHover);
}
.btnRed:active{
  background-color: var(--cancelClick);
}
body {
  background-color: #f9f9f9;
}

