        aside {
            position: fixed;
            left: 0;
            width: 20%;
            height: 100%;
            top: 0;
        }
        


        /*------------------------------------------------  

                        reset y variables

        --------------------------------------------------*/

        /*-------   aside   -----------*/

        aside {
            background-image: url(../components/images/fondo-logo.jpg);
            background-repeat: no-repeat;
            background-size: cover;
            background-color: rgba(16, 108, 160, 0.34);
            background-blend-mode: hue;
        }

        aside > div {
            background-color: rgba(64, 84, 178, 0.67);
            text-align: center;
        }

        aside > div > img {
            width: 120px;
            margin: 20px 0 10px;
            border-radius: 50%;
            border: 4px solid white;
        }

        aside > div > div >h3,
        aside > div > div >p,
        aside > a {
            color: white;
            font-weight: bolder;
        }

        aside > div > div > h3 {
            line-height: 2;
            background-color: rgba(0, 0, 0, 0.2);
            font-size: 18px;
            text-align: center;
        }

        aside > div > div > p {
            line-height: 1;
            font-size: 12px;
            padding: 7px 0 15px;
            border-bottom: 2px solid rgba(255, 255, 255, 0.8);
        }

        aside > a {
            display: block;
            font-size: 16px !important;
            line-height: 2;
            text-align: left;
            padding: 0 0 0 20px;
            color: rgba(255, 255, 255, 0.9);
            transition: all 0.1s;
            border-top: 2px solid rgba(255, 255, 255, 0.0);
            border-bottom: 2px solid rgba(255, 255, 255, 0.0);
        }

        aside > a:nth-child(2) {
            display: none;
        }

        aside > a:hover {
            padding-left: 40px;
            color: rgba(255, 255, 255, 1);
        }

        aside > a:hover::before {
            content: "•   ";
        }
        aside>span{
            display:none;
        }

        @media all and (max-width:1024px) {

            /*-------   estructura   -----------*/

            aside {
                width: 30%;
                overflow-y:auto;
            }
        }

        @media (max-width:720px) {

            /*-------   estructura   -----------*/
            /*-------   Menu   -----------*/
            aside {
                height: 278px;
                transition: all 0.3s;
                overflow: hidden;
                width: 100%;
                position: relative;
                left:0;
                top:0;
            }
            aside > div{
                height: 75px;
                display: grid;
                grid-template-columns: 60px auto 60px;
                grid-column-gap: 20px;
               
            }
            aside > div > img{
                width: 60px;
                border: 2px solid white;
                margin: 5px;
            }
            aside>div>div{
                display: flex;
                flex-flow: column;
                justify-content: center;
            }
            aside > div > div > p,aside > div > div > h3{
                text-align: left;
                line-height: 1.3;
            }
            aside > div > div > h3{
                font-size: 15px;
                background-color: transparent;
            }
            aside > div > div > p{
                padding: 5px 0 0;
                border: none;
            }
            aside {
                height: 75px;
            }
            aside>span{
                color:white;
                padding: 5px;
                position: absolute;
                right:5%;
                font-size: 20px;
                font-weight: bold;
                top:10px;
                cursor:pointer;
                display:block;
            }
            aside > a:nth-child(2) {
                display: block;
                margin-top: -60px;
                margin-bottom: 15px;
                position: relative;
                color: transparent;
            }

            aside > a:nth-child(2)::after {
                content: "";
                position: absolute;
                opacity: 1;
                width: 50px;
                top: 18%;
                right: 15px;
                box-shadow: 0px 12px white, 0px 24px white;
                height: 3px;
                background-color: white;
            }

            aside > a {
                text-align: center;
                border-bottom: 1px solid rgba(255, 255, 255, 0.3);
            }
        }
