.contanerCalc{
    display: flex;
    flex-flow: row;
    justify-content: center;
    z-index:900;
    position: relative;
}
.calculadora{
    width:100%;
    border-radius: 1vmin;
    padding:2vmin;
    display:flex;
    flex-flow: row wrap;
    overflow: hidden;
    justify-content: center;
    align-content: flex-start;
    box-shadow: 0px 1px 2px black;
    background: white;
}
.calculadora > h2, label{
    font-weight: bold;
    padding: 0 1vmin;
    width:100%;
    line-height: 2;
    display:flex;
    flex-flow:row wrap;
    align-content: center;
    align-items: center;
    justify-content: center;
}
.calculadora img{
    width:8%;
    margin:2vmin;
    transition: all .1s !important;
    border-radius:50%;
    box-shadow:0px 0px 5px rgba(0, 0, 0, 0.774);
}
.calculadora img:active{
    transform:scale(1.5);
}
.calculadora > h2{
    text-align: center;
}
.calculadora input{
    line-height: 2;
    padding: 0 2vmin;
}
.calculadora > span{
    width:100%;
    text-align:center;
    margin:1vmin auto;
}
.calculadora > span > input[type='number']{
    width:70%;
    background:var(--background-color);
    border:1px solid rgb(192, 191, 191);
    border-radius: 1vmin;
    margin:0 auto;
}
.calculadora > a{
    background:rgb(97, 206, 112);
    color:white;
    width:50%;
    margin:2vmin 0;
    line-height: 2;
    text-align:center;
}
.calculadora > .pop{
    top:var(--hHeader);
    right:-100%;
    position:fixed;
    transition:all .2s linear;
    margin:1vmin;
}

@media(max-width:720px){
    .welcome > div{
        width:95%;
        margin:12vmin auto;
    }
    
    .secure_process > div{
        grid-column: 1 / 7;
    }
    .secure_process > article{
        grid-column: 1 / 7;
    }
    .whatsapp > h2{
        width:100%;
    }
    .whatsapp > a{
        width:70%;
        margin: 4vmin auto;
    }
    main > section{
        grid-column: 1 / 17;
        padding:4vmin 0;
    }
    .calculadora{
        width:100%;
    }
    .calculadora > img{
        margin:3vmin;
    }
}