.containerTazaActual{
    width:70%;
    display: grid;
    grid-template-columns: repeat(4,180px);
    padding:10px 0;
    grid-gap:2px;
    position: relative;
    margin: 0 auto;
}
.ActMonedas{
    width: 180px;
    padding: 10px;
    box-shadow: 0px 0px 1px #eaeaea;
    display: flex;
    flex-flow: column;
    align-items: center;
    background: white;
}
.ActMonedas>img{
    width: 100px;
}
.ActMonedas>div{
    position: relative;
}
.ActMonedas>div>input{
    width: 90%;
    margin: 0;
}
.ActMonedas>div>span{
    background-color: rgb(97, 206, 112);
    border-radius: 5px;
    position: absolute;
    top: 45%;
    right: -2%;
    height: 30px;
    width: 30px;
    padding: 5px;
    display: flex;
    text-align: center;
    justify-content: center;
}
.ActMonedas>div>label{
    line-height: 2;
    font-size: 14px;
    font-weight: lighter;
}
.ActMonedas>div>span>img{
    height: 20px;
}
.ActMonedas>div>span:hover{
    background-color: rgb(68, 201, 85);
}
.ActMonedas>div>span:active{
    background-color: rgb(53, 170, 68);  
}