input,select{
    border: 1px solid #eaeaea;
    background-color: #fff;
    color: #666;
    padding: .75em;
    border-radius: 5px;
    font-size: 1rem;
    margin: 10px 0px;
    line-height: 1;
}
/*------  Texto SPAN resaltado  ------------*/
p>span{
    font-weight: bolder;
    color: #106ca0;
    text-transform: uppercase;
}
/*--------------------

cartas de cuentas bancarias

---------------------------------*/
.containerCards{
    width: 100%;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
    
}
.DatosBancarios{
    overflow: hidden;
    width:90%;
    border-radius: 10px;
    border: 2px solid #eaeaea;
    box-shadow: 2px 2px 1px #eaeaea;
    position: relative;
}
@media(min-width:512px){
    .containerCards{
        width: 100%;
        display: flex;
        flex-flow: row wrap;
    }
    .DatosBancarios{
        overflow: hidden;
        width:42%;
    }
}
.DatosBancarios::before{
    content: "";
    position: absolute;
    opacity: 0.15;
    z-index: -1;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../../svg/banco2.svg);
    background-size: 15%;
    background-position:right 105%;
    background-repeat: no-repeat;
}

.DatosBancarios>h2{
    display: block;
    text-transform: uppercase;
    background:#106ca0;
    color: #FFF;
    font-size: 26px;
    padding: 3px 10px;
    line-height: 36px;
}
.DatosBancarios>p{
    width: 80%;
    padding: 10px 20px;
    text-align: left;
    color: #696969;
    text-transform: capitalize;
    line-height: 1.5;
}


/*--------------------

Pasos para la compra

---------------------------------*/

/*------  Paso 2  ------------*/

.Paso2>div>label,.Paso2>div>select,.Paso2>div>input{
    display: block;
    text-align: left;
    width: 100%;
}
.Paso2>div>label::before{
    content: "*";
    color: rgba(255, 0, 0, 0.5);
    padding-right: 5px;
}
.Paso2>div>input{
    width: 95%;
}
