/*--------------------

agregar cuentas bancarias

---------------------------------*/

.AgregarBanco{
    margin: 0 auto;
    width: 90%;
    border-radius: 10px;
    border: 2px solid #eaeaea;
    padding: 2vmin;
    box-shadow: 2px 2px 1px #eaeaea;
    text-align: center;
    position: relative;
    display: grid;
    grid-template-columns: repeat(1,1fr);
}
@media(min-width:720px){
    .AgregarBanco{
        grid-template-columns: repeat(2,1fr);
    }
}
.AgregarBanco > div>a{
    display: block;
    margin: 0 auto;
}
.AgregarBanco > div>input,.AgregarBanco > div>label,.AgregarBanco > div>select{
    display: block;
    text-align: left;
    width: 100%;
}
.AgregarBanco > div>label,.AgregarBanco > div>select,.AgregarBanco > div>input{
    display: block;
    text-align: left;
    width: 100%;
}
.AgregarBanco > div>label::before{
    content: "*";
    color: rgba(255, 0, 0, 0.5);
    padding-right: 5px;
}
.AgregarBanco>input{
    width: 95%;
}